<template>
  <b-container class="bv-example-row">
    <b-row
      ><b-col>
        <div
          class="    
   loginForm"
        >
          <b-form @submit="onSubmit" @reset="onReset" v-if="show">
            <b-form-group
              id="input-group-1"
              label=" :"
              label-for="input-1"
              description="      ."
            >
              <b-form-input
                id="input-1"
                v-model="form.username"
                type="text"
                placeholder=" "
                required
              ></b-form-input>
            </b-form-group>

            <b-form-group
              id="input-group-2"
              label=" :"
              label-for="input-2"
            >
              <b-form-input
                id="input-2"
                v-model="form.password"
                type="password"
                required
                placeholder=" "
              ></b-form-input>
            </b-form-group>

            <b-button type="submit" variant="primary"></b-button>
            <b-button type="reset" variant="danger"></b-button>
          </b-form>
        </div>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import base from "./set";
import axios from "axios";
export default {
  data() {
    return {
      form: {
        username: "",
        password: ""
      },

      show: true
    };
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      //alert(JSON.stringify(this.form));
      // localStorage.auth = "kk";

      let url =
        base.baseUrl +
        "login?username=" +
        this.form.username +
        "&password=" +
        this.form.password;
      //alert(url);
      self = this;
      axios
        .get(url)
        .then(function(response) {
          // alert(response);
          console.log(response);
          if (response.data.true) {
            localStorage.auth = response.data.token;
            //location.reload();
            self.$router.push("/addStudents");
          } else alert(response.data.msg);
        })
        .catch(function(error) {
          alert("sponse");
        });
    },
    onReset(evt) {
      evt.preventDefault();
      // Reset our form values
      this.form.username = "";
      this.form.password = "";

      // Trick to reset/clear native browser form validation state
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      });
    }
  }
};
</script>

