<template>
  <div>
    <notifications group="foo" />
    <router-view @click="toggleClass()" :key="$route.fullPath"></router-view>
  </div>
</template>

<script>
import base from "./set";

import axios from "axios";

//import light from "./white.css";

export default {
  data() {
    return {
      lightIsActive: localStorage.light
    };
  },
  methods: {
    _addDarkTheme() {
      let darkThemeLinkEl = document.createElement("link");
      darkThemeLinkEl.setAttribute("rel", "stylesheet");
      darkThemeLinkEl.setAttribute(
        "href",
        "http://localhost/vue-black/src/dark-mode.css"
      );
      darkThemeLinkEl.setAttribute("id", "dark-theme-style");

      let docHead = document.querySelector("head");
      docHead.append(darkThemeLinkEl);
    },
    _removeDarkTheme() {
      let darkThemeLinkEl = document.querySelector("#dark-theme-style");
      let parentNode = darkThemeLinkEl.parentNode;
      parentNode.removeChild(darkThemeLinkEl);
    },
    darkThemeSwitch() {
      let darkThemeLinkEl = document.querySelector("#dark-theme-style");
      if (!darkThemeLinkEl) {
        this._addDarkTheme();
      } else {
        this._removeDarkTheme();
      }
    },
    disableRTL() {
      if (!this.$rtl.isRTL) {
        this.$rtl.disableRTL();
      }
    },
    toggleNavOpen() {
      let root = document.getElementsByTagName("html")[0];
      root.classList.toggle("nav-open");
    }
  },
  mounted() {
    if (localStorage.light) {
      let darkThemeLinkEl = document.createElement("link");
      darkThemeLinkEl.setAttribute("rel", "stylesheet");
      darkThemeLinkEl.setAttribute(
        "href",
        "http://localhost/vue-black/src/dark-mode.css"
      );
      darkThemeLinkEl.setAttribute("id", "dark-theme-style");

      let docHead = document.querySelector("head");
      docHead.append(darkThemeLinkEl);
    }
    this.$watch("$route", this.disableRTL, { immediate: true });
    this.$watch("$sidebar.showSidebar", this.toggleNavOpen);
    //localStorage.removeItem("auth");
    //location.reload();

    if (!localStorage.auth) this.$router.push("/login");
  },
  watch: {
    $route(to, from) {
      self = this;
      //alert(to.path);
      if (to.path != "/login" && !localStorage.auth)
        this.$router.push("/login");

      let url = base.baseUrl + "checkToken";
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          if (response.data.true == 0) {
            localStorage.removeItem("auth");
            self.$router.push("/login");
          }
        })
        .catch(function(error) {
          //alert("sponse");
        });
    }
  }
};
</script>

<style lang="scss"></style>
